<template>
  <v-row class="hundredAlmost">
    <v-row>
      <v-col>
        <v-btn @click="logRect" color="white"> Turn Timer: {{ turnTimer }} </v-btn>
      </v-col>
      <v-col>
        <v-btn color="white" :class="isHidden" @click="skip">
          Finish Turn
        </v-btn>
      </v-col>
    </v-row>
    <v-row class="hundred">
      <img
        id="pen"
        style="position: relative;"
        width="30px"
        src="../assets/fountain_pen.svg"
      />
      <canvas
        id="canvas"
        width="800px"
        height="500px"
        @mousedown="drawEnabled"
        @mouseup="drawDisabled"
        @mousemove="draw"
        @touchmove="draw"
        @touchstart="drawEnabled"
        @touchend="drawDisabled"
      >
      </canvas>
    </v-row>
  </v-row>
</template>

<script>
import {
  MOUSE_MOVE,
  MOUSE_UP,
  MOUSE_DOWN,
  SET_CANVAS,
  FINISH_TURN,
  SET_PEN,
  ADJUST_PEN,
} from "../constants/mutation-types";

export default {
  name: "Canvas",
  props: {},
  data: () => {
    return {
      now: Date.now(),
    };
  },
  methods: {
    logRect: function () {
        let c = document.getElementById("canvas");
        var rect = c.getBoundingClientRect();
        console.log(rect.top, rect.right, rect.bottom, rect.left);

    },
    skip: function() {
      if (this.$store.getters.isActive) {
        this.$store.commit(FINISH_TURN);
      }
    },
    draw: function(e) {
      if (e) {
        e.preventDefault();
      }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_MOVE, {
          x: e.offsetX,
          y: e.offsetY,
          clientX: e.clientX,
          clientY: e.clientY - 90,
        });
      }
    },
    drawEnabled: function(e) {
      if (e) {
        e.preventDefault();
      }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_DOWN, {
          x: e.offsetX,
          y: e.offsetY,
          clientX: e.clientX,
          clientY: e.clientY,
        });
      }
    },
    drawDisabled: function(e) {
      if (e) {
        e.preventDefault();
      }
      if (this.$store.getters.isActive) {
        this.$store.commit(MOUSE_UP, {});
      }
    },
  },
  computed: {
    isHidden() {
      return this.$store.getters.isActive ? "" : "hidden";
    },
    turnOrder() {
      return this.$store.state.turnOrder;
    },
    turnTimer() {
      let time = new Date(this.$store.state.turnTimeEnd).getTime();
      return Math.round((time - this.now) / 1000);
    },
    playerMap() {
      return this.$store.state.playerMap;
    },
  },
  mounted() {
    let c = document.getElementById("canvas");
    let ctx = c.getContext("2d");
    this.$store.commit(SET_CANVAS, { ctx });
    let p = document.getElementById("pen");
    p.style.position = "absolute";
    this.$store.commit(SET_PEN, { p });
  },
  created() {
    setInterval(() => {
      this.now = Date.now();
      let c = document.getElementById("canvas");
      this.$store.commit(ADJUST_PEN, { rect: c.getBoundingClientRect() });
    }, 1000);
  },
};
</script>

